* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 80vh;
}

.navbar {
  background-color: #212529 !important;
  font-weight: 900;
}

/* Login and Registration form */
.background {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background: url("./images/pexels-shahid-sultan-6595970.jpg");
  background-size: cover;
}

.form {
  background-color: rgba(255, 255, 255, 0.33);
  box-shadow: 0px 8px 32px rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(3px);
  padding: 16px 16px 16px 16px;
  border-radius: 16px;
}
/* Login and Registration form */

/* transaction page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  margin: 30px 30px;
  box-shadow: 0 0 3px gray;
  border-radius: 6px;
}
/* transaction page */
